import { App, Button, Flex, Popover, TableProps, Tag, Typography } from 'antd';
import { Deployment } from '../types.ts';
import { tagsColorMap } from '../../../utils/tags.ts';
import { useMemo } from 'react';
import { DeleteOutlined, EditOutlined, MoreOutlined } from '@ant-design/icons';
import { useMutation } from '@tanstack/react-query';
import { AxiosError, AxiosResponse } from 'axios';
import { deleteAppDeployment } from '../../../api/client.ts';

const { Paragraph, Text } = Typography;

export const useDeploymentColumns = (
  application: string,
  onSuccessAction: () => void,
  onEditAction: (deploymentName: string) => void
) => {
  const { message, modal } = App.useApp();

  const { mutate } = useMutation<
    AxiosResponse,
    AxiosError,
    { app: string; deployment: string }
  >({
    mutationFn: deleteAppDeployment,
    onSuccess: () => {
      message.success('Successfully deleted');
      onSuccessAction();
    },
    onError: (error) => {
      message.open({
        type: 'error',
        content:
          (error?.response?.data as string) || "Couldn't delete development",
      });
    },
  });

  const columns: TableProps<Deployment>['columns'] = useMemo(
    () => [
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        width: 200,
        render: (text, _, index) => (
          <Tag color={tagsColorMap[index]} key={index}>
            {text}
          </Tag>
        ),
      },
      {
        title: 'Key',
        dataIndex: 'key',
        key: 'key',
        width: 400,
        render: (key) => {
          return <Paragraph copyable>{key}</Paragraph>;
        },
      },
      {
        title: 'Last release',
        dataIndex: 'package',
        key: 'package',
        render: (packageInfo) => {
          let releaseSource: string | null = null;
          if (packageInfo?.releaseMethod === 'Promote') {
            releaseSource = `Promoted ${packageInfo.originalLabel} from "${packageInfo.originalDeployment}"`;
          } else if (packageInfo?.releaseMethod === 'Rollback') {
            const labelNumber: number = parseInt(
              packageInfo.label.substring(1)
            );
            const lastLabel: string = 'v' + (labelNumber - 1);
            releaseSource = `Rolled back ${lastLabel} to ${packageInfo.originalLabel}`;
          }
          return (
            <>
              <Text strong>{packageInfo?.label}</Text>
              {releaseSource && (
                <>
                  <Text>{' | '}</Text>
                  <Text>{packageInfo?.appVersion}</Text>
                </>
              )}

              <br />
              <Text type="secondary">{releaseSource}</Text>
            </>
          );
        },
      },
      {
        title: 'Actions',
        key: 'actions',
        width: 100,
        align: 'center',

        render: (_, record) => (
          <Popover
            content={
              <Flex vertical gap="small">
                <Button
                  onClick={(event) => {
                    event.stopPropagation();

                    onEditAction(record?.name);
                  }}
                  type="text"
                  icon={<EditOutlined />}
                >
                  Edit
                </Button>
                <Button
                  onClick={(event) => {
                    event.stopPropagation();

                    modal.confirm({
                      icon: null,
                      title: 'Confirm',
                      content: 'Are you sure you want to remove deployment?',
                      onOk: () => {
                        mutate({
                          app: application,
                          deployment: record?.name,
                        });
                      },
                    });
                  }}
                  type="text"
                  danger
                  icon={<DeleteOutlined />}
                >
                  Delete
                </Button>
              </Flex>
            }
            trigger="click"
          >
            <Button
              onClick={(event) => {
                event.stopPropagation();
              }}
              icon={<MoreOutlined />}
              type="text"
            />
          </Popover>
        ),
      },
    ],
    [application, modal, mutate, onEditAction]
  );

  return columns;
};
