import { type FC, useState, type ReactNode, useEffect } from 'react';
import {
  Drawer,
  Divider,
  Switch,
  Input,
  Row,
  Col,
  Form,
  Space,
  Button,
  Progress,
  Slider,
  Select,
  Modal,
  type FormProps,
  App,
} from 'antd';
import { Metrics, ReleaseItem, ReleasePatch } from '../types.ts';
import dayjs from 'dayjs';
import { formatBytes } from '../../../utils/formatBytes.ts';
import { useMutation } from '@tanstack/react-query';
import { AxiosError, AxiosResponse } from 'axios';
import { patchRelease, promoteRelease } from '../../../api/client.ts';

type ReleaseDetailsProps = {
  isOpen: boolean;
  application: string;
  deployment: string | null;
  deployments: string[];
  metrics?: Metrics;
  release: ReleaseItem | null;
  onClose: () => void;
  onSuccess: () => void;
};

type PromoteForm = {
  deployment: string;
};

interface DescriptionItemProps {
  title: string;
  content: ReactNode;
}

const { TextArea } = Input;

const DescriptionItem = ({ title, content }: DescriptionItemProps) => (
  <div>
    <p style={{ fontWeight: 'bold' }}>{title}:</p>
    {content}
  </div>
);

const ReleaseDetails: FC<ReleaseDetailsProps> = ({
  isOpen,
  application,
  deployment,
  deployments,
  metrics,
  release,
  onSuccess,
  onClose,
}) => {
  const [form] = Form.useForm<ReleasePatch>();
  const [promoteForm] = Form.useForm<PromoteForm>();
  const { message } = App.useApp();
  const [isEditMode, setEditMode] = useState(false);
  const [isPromoteModal, setPromoteModal] = useState(false);

  useEffect(() => {
    if (isOpen && release) {
      form.resetFields();
      form.setFieldValue('appVersion', release?.appVersion);
      form.setFieldValue('isMandatory', release?.isMandatory);
      form.setFieldValue('isDisabled', release?.isDisabled);
      form.setFieldValue('rollout', release?.rollout || 100);
    }
  }, [form, release, isOpen]);

  const deploymentsToPromote =
    deployments
      ?.filter((item) => item !== deployment)
      ?.map((item: string) => ({
        value: item,
        label: item,
      })) || [];

  const { mutate, isPending } = useMutation<
    AxiosResponse,
    AxiosError,
    { app: string; deployment: string; release: ReleasePatch }
  >({
    mutationFn: patchRelease,
    onSuccess: () => {
      onSuccess();
      onCloseDrawer();
    },
    onError: (error) => {
      message.open({
        type: 'error',
        duration: 5,
        content: (error?.response?.data as string) || "Couldn't release update",
      });
    },
  });

  const onClosePromote = () => {
    setPromoteModal(false);
    promoteForm.resetFields();
  };

  const { mutate: mutatePromote, isPending: isPendingPromote } = useMutation<
    AxiosResponse,
    AxiosError,
    { app: string; deployment: string; destDeploymentName: string }
  >({
    mutationFn: promoteRelease,
    onSuccess: () => {
      message.success('Successfully promoted');
      onClosePromote();
    },
    onError: (error) => {
      message.open({
        type: 'error',
        duration: 5,
        content:
          (error?.response?.data as string) ||
          'This release is already the latest release for the selected deployment.',
      });
    },
  });

  const onCloseDrawer = () => {
    onClose();
    form.resetFields();
    setEditMode(false);
  };

  const onSubmit: FormProps<ReleasePatch>['onFinish'] = (fields) => {
    if (deployment) {
      const newFields = {
        ...fields,
      };
      if (fields.rollout === release?.rollout || release?.rollout === null) {
        delete newFields.rollout;
      }
      mutate({
        app: application,
        deployment,
        release: newFields,
      });
    }
  };

  const onPromoteSubmit: FormProps<PromoteForm>['onFinish'] = (fields) => {
    if (deployment) {
      mutatePromote({
        app: application,
        deployment,
        destDeploymentName: fields.deployment,
      });
    }
  };

  return (
    <Drawer
      title={`Release: ${release?.label}`}
      onClose={onCloseDrawer}
      open={isOpen}
      width={640}
      destroyOnClose
      extra={
        isEditMode ? (
          <Space>
            <Button onClick={() => setEditMode(false)} loading={isPending}>
              Cancel
            </Button>
            <Button
              type="primary"
              onClick={() => form.submit()}
              loading={isPending}
            >
              Release changes
            </Button>
          </Space>
        ) : (
          <Space>
            <Button onClick={() => setPromoteModal(true)}>Promote</Button>
            <Button onClick={() => setEditMode(true)}>Edit</Button>
          </Space>
        )
      }
    >
      <Modal
        title={`Promote '${deployment}' to a different deployment`}
        open={isPromoteModal}
        okText="Promote"
        onOk={() => promoteForm.submit()}
        onCancel={onClosePromote}
        loading={isPendingPromote}
      >
        <Form
          form={promoteForm}
          onFinish={onPromoteSubmit}
          style={{ paddingTop: 10 }}
        >
          <Form.Item
            name="deployment"
            label="Deployment"
            rules={[
              {
                required: true,
                message: 'Please enter target deployment',
              },
            ]}
          >
            <Select options={deploymentsToPromote} />
          </Form.Item>
        </Form>
      </Modal>
      <Form form={form} onFinish={onSubmit} disabled={isPending}>
        <Row>
          <Col span={12}>
            <DescriptionItem title="Deployment" content={deployment} />
          </Col>
          <Col span={12}>
            <DescriptionItem
              title="Target Versions"
              content={
                isEditMode ? (
                  <Form.Item
                    name="appVersion"
                    rules={[
                      {
                        required: true,
                        message: 'Please enter target version',
                      },
                    ]}
                  >
                    <Input placeholder="Please enter target version" />
                  </Form.Item>
                ) : (
                  release?.appVersion
                )
              }
            />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <DescriptionItem
              title="Mandatory"
              content={
                isEditMode ? (
                  <Form.Item name="isMandatory" valuePropName="checked">
                    <Switch checkedChildren="Yes" unCheckedChildren="No" />
                  </Form.Item>
                ) : release?.isMandatory ? (
                  'Yes'
                ) : (
                  'No'
                )
              }
            />
          </Col>
          <Col span={12}>
            <DescriptionItem
              title="Disabled status"
              content={
                isEditMode ? (
                  <Form.Item
                    name="isDisabled"
                    valuePropName="checked"
                    extra="When disabled, this update will not be available to your users."
                  >
                    <Switch checkedChildren="Yes" unCheckedChildren="No" />
                  </Form.Item>
                ) : release?.isDisabled ? (
                  'Disabled'
                ) : (
                  'Enabled'
                )
              }
            />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <DescriptionItem
              title="Release Method"
              content={release?.releaseMethod}
            />
          </Col>
          <Col span={12}>
            <DescriptionItem
              title="File size"
              content={formatBytes(release?.size || 0)}
            />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <DescriptionItem
              title="Released By"
              content={release?.releasedBy}
            />
          </Col>
          <Col span={12}>
            <DescriptionItem
              title="Released On"
              content={dayjs(release?.uploadTime).format('MMM D, hh:mm A')}
            />
          </Col>
        </Row>
        <Divider />
        <Row>
          <Col span={12}>
            <DescriptionItem title="Active" content={metrics?.active || '-'} />
          </Col>
          <Col span={12}>
            <DescriptionItem
              title="Downloaded"
              content={metrics?.downloaded || '-'}
            />
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <DescriptionItem
              title="Installed"
              content={metrics?.installed || '-'}
            />
          </Col>
          <Col span={12}>
            <DescriptionItem title="Failed" content={metrics?.failed || '-'} />
          </Col>
        </Row>
        <Divider />
        <Row>
          <Col span={20}>
            <DescriptionItem
              title="Rollout"
              content={
                isEditMode ? (
                  <Form.Item
                    name="rollout"
                    initialValue={release?.rollout || 100}
                    extra="The percentage of users eligible for this update (value can only be increased)."
                  >
                    <Slider
                      disabled={
                        release?.rollout === null || release?.rollout === 100
                      }
                      min={release?.rollout === null ? 0 : release?.rollout}
                      max={100}
                      tooltip={{ open: true }}
                    />
                  </Form.Item>
                ) : (
                  <Progress
                    steps={10}
                    percent={release?.rollout || 100}
                    status="normal"
                  />
                )
              }
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <DescriptionItem
              title="Description"
              content={
                isEditMode ? (
                  <Form.Item name="description">
                    <TextArea rows={3} />
                  </Form.Item>
                ) : (
                  release?.description || '-'
                )
              }
            />
          </Col>
        </Row>
      </Form>
    </Drawer>
  );
};

export default ReleaseDetails;
