import { type FC } from 'react';
import { Form, Input, Modal, type FormProps, App } from 'antd';
import { useMutation } from '@tanstack/react-query';
import { addNewAppDeployment } from '../../../api/client.ts';
import { AxiosError, type AxiosResponse } from 'axios';

type DeploymentProps = {
  appName: string;
  isOpen: boolean;
  onSuccess: () => void;
  onCancel: () => void;
};

type FieldType = {
  name: string;
};

export const AddDeploymentModal: FC<DeploymentProps> = ({
  appName,
  isOpen,
  onSuccess,
  onCancel,
}) => {
  const [form] = Form.useForm<FieldType>();
  const { message } = App.useApp();

  const { mutate, isPending } = useMutation<
    AxiosResponse,
    AxiosError,
    { app: string; deployment: { name: string } }
  >({
    mutationFn: addNewAppDeployment,
    onSuccess: onSuccess,
    onError: (error) => {
      message.open({
        type: 'error',
        content:
          (error?.response?.data as string) || "Couldn't create new deployment",
      });
    },
  });

  const onSubmit: FormProps<FieldType>['onFinish'] = ({ name }) => {
    mutate({
      app: appName,
      deployment: {
        name,
      },
    });
  };

  const onFormCancel = () => {
    form.resetFields();
    onCancel();
  };

  return (
    <Modal
      title="New deployment"
      open={isOpen}
      okText="Add new deployment"
      onOk={() => form.submit()}
      confirmLoading={isPending}
      onCancel={onFormCancel}
    >
      <Form form={form} layout="vertical" onFinish={onSubmit}>
        <Form.Item<FieldType>
          label="Deployment name"
          name="name"
          rules={[
            {
              required: true,
              message: 'Please input deployment name!',
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  );
};
