import { FC, useEffect, useState, lazy, Suspense } from 'react';
import { useQuery } from '@tanstack/react-query';
import dayjs from 'dayjs';

import {
  fetchDeployments,
  fetchMetrics,
  fetchReleases,
} from '../../../api/client.ts';
import {
  Flex,
  Skeleton,
  Table,
  type TableProps,
  Select,
  Typography,
  Progress,
  Button,
} from 'antd';
import { CloudDownloadOutlined } from '@ant-design/icons';
import { Deployment, ReleaseItem } from '../types.ts';
import ReleaseDetails from './ReleaseDetails.tsx';

const { Title, Text } = Typography;

const Instruction = lazy(() => import('./Instruction.tsx'));

type ReleasesProps = {
  application: string;
};

const downloadFile = (fileUrl: string, fileName?: string) => {
  const link = document.createElement('a');
  link.href = fileUrl;
  link.download = fileName || 'download';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};

const columns: TableProps<ReleaseItem>['columns'] = [
  {
    hidden: true,
    dataIndex: 'label',
    key: 'label',
  },
  {
    title: 'Release',
    key: 'release',
    width: 200,
    defaultSortOrder: 'descend',
    sorter: (a, b) => {
      return parseInt(a.label.substring(1)) - parseInt(b.label.substring(1));
    },
    render: (_, record) => {
      let releaseSource: string | null = null;
      if (record.releaseMethod === 'Promote') {
        releaseSource = `Promoted ${record.originalLabel} from "${record.originalDeployment}"`;
      } else if (record.releaseMethod === 'Rollback') {
        const labelNumber: number = parseInt(record.label.substring(1));
        const lastLabel: string = 'v' + (labelNumber - 1);
        releaseSource = `Rolled back ${lastLabel} to ${record.originalLabel}`;
      }

      return (
        <>
          <Text strong>{record.label}</Text>
          <br />
          <Text>{record.releasedBy}</Text>
          {releaseSource && (
            <>
              <br />
              <Text type="secondary">{releaseSource}</Text>
            </>
          )}
        </>
      );
    },
  },
  {
    title: 'Target version',
    dataIndex: 'appVersion',
    key: 'appVersion',
    width: 80,
    render: (appVersion) => appVersion,
  },
  {
    title: 'Status',
    dataIndex: 'isDisabled',
    key: 'isDisabled',
    width: 50,
    render: (isDisabled) => (isDisabled ? 'Disabled' : 'Enabled'),
  },
  {
    title: 'Mandatory',
    dataIndex: 'isMandatory',
    key: 'isMandatory',
    width: 60,
    render: (isMandatory) => (isMandatory ? 'Yes' : 'No'),
  },
  {
    title: 'Rollout',
    dataIndex: 'rollout',
    key: 'rollout',
    width: 60,
    render: (rollout) => (
      <Progress steps={4} percent={rollout || 100} status="normal" />
    ),
  },
  {
    title: 'Date',
    dataIndex: 'uploadTime',
    key: 'uploadTime',
    width: 100,
    render: (uploadTime) => dayjs(uploadTime).format('MMM D, hh:mm A'),
  },
  {
    title: 'Bundle',
    dataIndex: 'blobUrl',
    key: 'blobUrl',
    width: 100,
    align: 'center',
    render: (blobUrl) => {
      return (
        <Button
          color="primary"
          variant="link"
          onClick={(event) => {
            event.stopPropagation();
            downloadFile(blobUrl);
          }}
          icon={<CloudDownloadOutlined style={{ fontSize: 24 }} />}
        />
      );
    },
  },
];

const Releases: FC<ReleasesProps> = ({ application }) => {
  const [deployment, setDeployment] = useState<Deployment | null>(null);
  const [isDetailsOpen, setIsDetailsOpen] = useState(false);
  const [selectedRelease, setRelease] = useState<ReleaseItem | null>(null);

  const {
    data: deployments,
    isPending: isDeploymentsPending,
    isError,
  } = useQuery({
    queryKey: ['deployments', application],
    queryFn: () => fetchDeployments(application),
    select: (app) => app.deployments || [],
  });

  const { data, isPending, fetchStatus, refetch } = useQuery({
    queryKey: ['releases', application, deployment?.name || ''],
    queryFn: () => fetchReleases(application, deployment?.name || ''),
    enabled: !!deployment,
  });

  const { data: metrics, refetch: refetchMetrics } = useQuery({
    queryKey: ['metrics', application, deployment?.name || ''],
    queryFn: () => fetchMetrics(application, deployment?.name || ''),
    enabled: !!deployment,
  });

  useEffect(() => {
    if (!deployment && deployments?.length > 0) {
      setDeployment(deployments[0]);
    }
  }, [deployment, deployments]);

  if (!isDeploymentsPending && deployments?.length === 0) {
    return null;
  }

  return (
    <>
      <Flex align="center" justify="start">
        <Title level={2}>Releases</Title>
        <Select
          style={{ minWidth: 200, marginLeft: 16, marginTop: 15 }}
          loading={isDeploymentsPending}
          value={deployment?.name}
          onChange={(value: string) => {
            setDeployment(
              deployments?.find((item: Deployment) => item.name === value)
            );
          }}
          disabled={isError}
          options={
            deployments?.map((item: Deployment) => ({
              value: item.name,
              label: item.name,
            })) || []
          }
        />
      </Flex>

      {isError ? null : (
        <Skeleton
          loading={
            isDeploymentsPending ||
            (isPending && fetchStatus === 'fetching') ||
            !deployment
          }
        >
          {deployment && data?.history?.length > 0 ? (
            <Table<ReleaseItem>
              rowKey="label"
              columns={columns}
              dataSource={data.history}
              pagination={false}
              onRow={(record) => ({
                onClick: () => {
                  setRelease(record);
                  setIsDetailsOpen(true);
                },
              })}
            />
          ) : (
            <Suspense fallback={<Skeleton loading />}>
              <Instruction
                application={application}
                deploymentKey={deployment?.key || ''}
                deploymentName={deployment?.name || ''}
              />
            </Suspense>
          )}
        </Skeleton>
      )}

      <ReleaseDetails
        application={application}
        deployment={deployment?.name || ''}
        deployments={deployments?.map((item: Deployment) => item?.name)}
        release={selectedRelease}
        isOpen={isDetailsOpen}
        metrics={metrics?.metrics[selectedRelease?.label || '']}
        onClose={() => setIsDetailsOpen(false)}
        onSuccess={() => {
          refetch();
          refetchMetrics();
          setRelease(null);
          setIsDetailsOpen(false);
        }}
      />
    </>
  );
};

export default Releases;
