import { type FC } from 'react';
import { Button, Card, Flex, Result, Skeleton, Table, Typography } from 'antd';
import { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import { fetchDeployments } from '../../../api/client.ts';
import { AddDeploymentModal } from './Add.tsx';
import { Deployment } from '../types.ts';
import { useDeploymentColumns } from './useDeploymentColumns.tsx';
import { EditDeploymentModal } from './Edit.tsx';
import { CloudUploadOutlined } from '@ant-design/icons';

const { Title } = Typography;

type DeploymentsProps = {
  application: string;
};

const Deployments: FC<DeploymentsProps> = ({ application }) => {
  const [showAddModal, setShowAddModal] = useState(false);
  const [selectedDeployment, setSelectedDeployment] = useState<string | null>(
    null
  );
  const [showEditModal, setShowEditModal] = useState(false);

  const { data, isPending, refetch, isError } = useQuery({
    queryKey: ['deployments', application],
    queryFn: () => fetchDeployments(application),
  });

  const onEditAction = (deploymentName: string) => {
    setSelectedDeployment(deploymentName);
    setShowEditModal(true);
  };

  const columns = useDeploymentColumns(application, refetch, onEditAction);

  return (
    <>
      <Flex align="center" justify="space-between">
        <Title level={2}>Deployments</Title>
        <Button
          disabled={isError}
          onClick={() => setShowAddModal(true)}
          type="primary"
        >
          Add new deployment
        </Button>
      </Flex>

      <Skeleton loading={isPending}>
        {data?.deployments?.length > 0 ? (
          <Table<Deployment>
            rowKey="name"
            columns={columns}
            dataSource={data.deployments}
            pagination={false}
          />
        ) : (
          <Card>
            <Result
              title="Start your deployment"
              subTitle="Create your first deployment environemnt"
              icon={<CloudUploadOutlined />}
              extra={[
                <Button
                  onClick={() => setShowAddModal(true)}
                  type="default"
                  key="console"
                  disabled={isError}
                >
                  Add new deployment
                </Button>,
              ]}
            ></Result>
          </Card>
        )}
      </Skeleton>
      <AddDeploymentModal
        isOpen={showAddModal}
        appName={application}
        onCancel={() => setShowAddModal(false)}
        onSuccess={() => {
          refetch();
          setShowAddModal(false);
        }}
      />
      <EditDeploymentModal
        isOpen={showEditModal}
        appName={application}
        deployment={selectedDeployment!}
        onCancel={() => {
          setSelectedDeployment(null);
          setShowEditModal(false);
        }}
        onSuccess={() => {
          refetch();
          setSelectedDeployment(null);
          setShowEditModal(false);
        }}
      />
    </>
  );
};

export default Deployments;
