import { useNavigate, useParams } from 'react-router';
import { Layout, Breadcrumb } from 'antd';

import SideBar from '../../../components/SideBar/SideBar.tsx';
import ContentFooter from '../../../components/Footer/Footer.tsx';
import Deployments from '../deployment/Deployments.tsx';
import Releases from '../release/Releases.tsx';

const { Content } = Layout;

const Application = () => {
  const params = useParams();
  const navigate = useNavigate();

  const breadcrumb = [
    {
      title: 'Home',
      onClick: () => navigate('/'),
    },
    {
      title: 'Applications',
      onClick: () => navigate('/applications'),
    },
    {
      title: params.application,
    },
  ];

  return (
    <SideBar>
      <Layout>
        <Content
          style={{
            paddingRight: 50,
            paddingTop: 20,
          }}
        >
          <Breadcrumb separator=">" items={breadcrumb} />

          {params?.application && (
            <Deployments application={params?.application} />
          )}

          {params?.application && (
            <Releases application={params?.application} />
          )}
        </Content>
      </Layout>
      <ContentFooter />
    </SideBar>
  );
};

export default Application;
