import { type FC, useEffect } from 'react';
import { Form, Input, Modal, type FormProps, App } from 'antd';
import { useMutation } from '@tanstack/react-query';
import { editAppDeployment } from '../../../api/client.ts';
import { AxiosError, type AxiosResponse } from 'axios';

type EditDeploymentModalProps = {
  appName: string;
  deployment: string;
  isOpen: boolean;
  onSuccess: () => void;
  onCancel: () => void;
};

type FieldType = {
  name: string;
};

export const EditDeploymentModal: FC<EditDeploymentModalProps> = ({
  appName,
  deployment,
  isOpen,
  onSuccess,
  onCancel,
}) => {
  const [form] = Form.useForm<FieldType>();
  const { message } = App.useApp();

  useEffect(() => {
    form.setFieldValue('name', deployment);
  }, [form, deployment]);

  const { mutate, isPending } = useMutation<
    AxiosResponse,
    AxiosError,
    {
      app: string;
      deployment: { name: string; newName: string };
    }
  >({
    mutationFn: editAppDeployment,
    onSuccess: onSuccess,
    onError: (error) => {
      message.open({
        type: 'error',
        content:
          (error?.response?.data as string) || "Couldn't update deployment",
      });
    },
  });

  const onSubmit: FormProps<FieldType>['onFinish'] = ({ name }) => {
    mutate({
      app: appName,
      deployment: {
        name: deployment,
        newName: name,
      },
    });
  };

  const onFormCancel = () => {
    form.resetFields();
    onCancel();
  };

  return (
    <Modal
      title="Edit deployment"
      open={isOpen}
      okText="Update deployment"
      onOk={() => form.submit()}
      confirmLoading={isPending}
      onCancel={onFormCancel}
    >
      <Form form={form} layout="vertical" onFinish={onSubmit}>
        <Form.Item<FieldType>
          label="Deployment name"
          name="name"
          rules={[
            {
              required: true,
              message: 'Please input deployment name!',
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  );
};
